import Vue from 'vue'
import VueRouter from 'vue-router'
import LayoutIndex from '@/views/Layout/index.vue'
import HomeIndex from '@/views/Layout/home.vue'
import AppsoIndex from '@/views/Layout/appso.vue'
import ShoppingCart from '@/views/Layout/shoppingCart.vue'
import UserIndex from '@/views/Layout/user.vue'
import store from '@/store/index.js'

// 路由懒加载
const LoginIndex = () => import('@/views/Login/index')
const MyOrderIndex = () => import('@/views/MyOrder/index.vue')
const ProdetailIndex = () => import('@/views/Prodetail/index.vue')
const SearchIndex = () => import('@/views/Search/index.vue')
const SearchListIndex = () => import('@/views/Search/list.vue')
const PayIndex = () => import('@/views/Pay/index.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: LayoutIndex,
    redirect: '/home',
    children: [
      { path: '/home', component: HomeIndex },
      { path: '/appso', component: AppsoIndex },
      { path: '/shoppingcart', component: ShoppingCart },
      { path: '/user', component: UserIndex }
    ]
  },
  { path: '/login', component: LoginIndex },
  { path: '/myorder', component: MyOrderIndex },
  { path: '/prodetail/:id', component: ProdetailIndex },
  { path: '/search', component: SearchIndex },
  { path: '/searchlist', component: SearchListIndex },
  { path: '/pay', component: PayIndex }
]

const router = new VueRouter({
  routes
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  // to: 即将要进入的目标路由对象
  // from: 当前导航正要离开的路由
  // next: 是一个函数，表示放行
  // next() 放行
  // next('/login') 强制跳转到登录页面
  const needLogin = ['/myorder', '/pay']
  if (!needLogin.includes(to.path)) {
    next()
    console.log('无需登录')
  } else {
    const token = store.state.user.userInfo.token
    console.log(token)
    if (token) {
      next()
    } else {
      next('/login')
    }
  }
})

export default router
