import { getCartList, updateCart, delCart } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCartList (state, cartList) {
      state.cartList = cartList
    },
    toggleCheck (state, id) {
      const goods = state.cartList.find(item => item.goods_id === id)
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCart (state, obj) {
      const goods = state.cartList.find(item => item.goods_id === obj.goods_id)
      goods.goods_num = obj.goods_num
    }
  },
  actions: {
    async getCart (context) {
      const res = await getCartList()
      console.log(res)
      // 后台返回的数据没有checked属性，需要手动添加，本地自己维护
      res.data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCartList', res.data.list)
    },
    async updateCartApi (context, obj) {
      // 修改后端数据库中的数据
      const res = await updateCart(obj.goods_id, obj.goods_num, obj.goods_sku_id)
      console.log(res)
      // 修改前端vuex中的数据
      context.commit('changeCart', obj)
    },
    async delCartApi (context) {
      // 删除后端数据库中的数据
      const selectId = context.getters.cartSelect.map(item => item.id)
      await delCart(selectId)
      Toast('删除成功')
      // 删除前端vuex中的数据
      context.commit('setCartList', context.state.cartList.filter(item => !item.isChecked))
    }
  },
  getters: {
    // 购物车总数量
    cartTotalCount (state) {
      return state.cartList.reduce((pre, cur) => pre + cur.goods_num, 0)
    },
    // 选中商品
    cartSelect (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选中商品数量
    cartSelectCount (state, getters) {
      return getters.cartSelect.reduce((pre, cur) => pre + cur.goods_num, 0)
    },
    // 选中商品总价格
    cartSelectTotalPrice (state, getters) {
      return getters.cartSelect.reduce((pre, cur) => pre + cur.goods.goods_price_min * cur.goods_num, 0).toFixed(2)
    },
    // 是否全选
    cartAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
