import { getInfo, setInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state: {
    userInfo: getInfo()
  },
  mutations: {
    setUserInfo (state, payload) {
      state.userInfo = payload
      setInfo(payload)
    }
  },
  actions: {
    // 因为有两个数据要清除，user和cart，所以放在了actions中
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('setCartList', [], { root: true })
    }
  }
}
