const INFP_KEY = 'wyq-shopping-info'
const SEARCH_HISTORY_KEY = 'wyq-shopping-search-history'

export const getSearchHistory = () => {
  const res = localStorage.getItem(SEARCH_HISTORY_KEY)
  return res ? JSON.parse(res) : []
}
export const setSearchHistory = (arr) => {
  localStorage.setItem(SEARCH_HISTORY_KEY, JSON.stringify(arr))
}

export const getInfo = () => {
  const res = localStorage.getItem(INFP_KEY)
  return res ? JSON.parse(res) : { userId: '', token: '' }
}
export const setInfo = (obj) => {
  localStorage.setItem(INFP_KEY, JSON.stringify(obj))
}
export const removeInfo = () => {
  localStorage.removeItem(INFP_KEY)
}
